import Vue from 'vue'
import VueRouter from 'vue-router'
import { authGuard } from "../auth/authGuard";

import ActiveJobs from '../views/ActiveJobs.vue'
import ArchivedJobs from '../views/ArchivedJobs.vue'
import AppliedJobs from '../views/AppliedJobs.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ActiveJobs',
    component: ActiveJobs,
    beforeEnter: authGuard
  },
  {
    path: '/ArchivedJobs',
    name: 'ArchivedJobs',
    component: ArchivedJobs,
    beforeEnter: authGuard
  },
  {
    path: '/AppliedJobs',
    name: 'AppliedJobs',
    component: AppliedJobs,
    beforeEnter: authGuard
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
