import Vue from 'vue'
import Vuex from 'vuex'
import JobsService from '../JobsService'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    activeJobs: [],
    appliedJobs: [],
    archivedJobs: [],
  },
  mutations: {
    setJobs(state, jobs) {
      let activeJobsArr = [];
      let archivedJobsArr = [];
      let appliedJobsArr = [];
      jobs.forEach(job => {
        if (job.interviews.length == 0 && job.archived == false) {
          appliedJobsArr.push(job);
        } else if (job.archived == false) {
          activeJobsArr.push(job);
        } else {
          archivedJobsArr.push(job);
        }
      });
      state.activeJobs = activeJobsArr;
      state.appliedJobs = appliedJobsArr;
      state.archivedJobs = archivedJobsArr;
    },
  },
  actions: {
    getJobs({ commit }, userId) {
      JobsService.getJobs(userId)
        .then(res => {
          commit('setJobs', res)
        });
    },
    updateJob({ dispatch }, job) {
      JobsService.updateJob(job)
        .then(() => {
          dispatch('getJobs', job.userId)
        })
    },
    createJob({ dispatch }, job) {
      console.log(job);
      JobsService.insertJob(job)
        .then(() => {
          dispatch('getJobs', job.userId)
        })
    }
  },
  modules: {
  }
})
