<template>
  <v-app id="my-job-tracker">
    <v-overlay :value="loading">
      <v-progress-circular indeterminate color="primary" size="100" width="15"></v-progress-circular>
    </v-overlay>
    <v-overlay :value="newJob">
      <v-card width="400">
        <form @submit.prevent="createJob">
          <v-row class='justify-center'>
            <v-col cols='10' class="text-center">
              <v-text-field v-model="job.company" label="Company Name" required></v-text-field>
              <v-text-field v-model="job.position" label="Position" required></v-text-field>
              <v-menu ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="job.appliedOn"
                transition="scale-transition" offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field v-model="job.appliedOn" label="Date Applied" prepend-icon="mdi-calendar" readonly
                    v-bind="attrs" v-on="on">
                  </v-text-field>
                </template>
                <v-date-picker v-model="job.appliedOn" no-title scrollable>
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="menu = false">
                    Cancel
                  </v-btn>
                  <v-btn text color="primary" @click="$refs.menu.save(job.appliedOn)">
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols='10'>
              <v-row class="ma-0">
                <v-btn class="mr-4" type="submit" color='primary'>
                  Submit
                </v-btn>
                <v-btn @click="cancel" color='error'>
                  Cancel
                </v-btn>
              </v-row>
            </v-col>
          </v-row>
        </form>
      </v-card>
    </v-overlay>
    <v-app-bar app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>The Job Tracker</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn @click='newJob = true' color='primary'>Add New Job</v-btn>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app>

      <v-divider></v-divider>

      <v-list v-if="$auth.isAuthenticated">
        <v-list-item v-for="[icon, text, tabName] in links" :key="icon" link @click='$router.push(tabName)'>
          <v-list-item-icon>
            <v-icon>{{ icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item @click='logout'>
          <v-list-item-icon>
            <v-icon>mdi-logout</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list>
      <v-list v-if="!$auth.isAuthenticated">
        <v-list-item @click='login'>
          <v-list-item-icon>
            <v-icon>mdi-login</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Login</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>


    <v-main>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
  import { getInstance } from './auth';


  export default {
    data: () => ({
      newJob: false,
      drawer: false,
      loading: true,
      job: {
        company: "",
        position: "",
        appliedOn: new Date().toISOString().substr(0, 10),
        interviews: [],
        interviewTime: [],
        archived: false,
      },
      links: [
        ['mdi-account-check', 'Active', '/'],
        ['mdi-email-send', 'Applied', 'AppliedJobs'],
        ['mdi-archive', 'Archived', 'ArchivedJobs'],
      ],
      menu: false,
    }),
    methods: {
      async createJob() {
        let job = this.job;
        job.userId = this.$auth.user.sub.split('|')[1]
        try {
          this.$store.dispatch('createJob', job);
          this.newJob = false;
        } catch (err) {
          console.log(err)
        }
      },
      // Log the user in
      login() {
        this.$auth.loginWithRedirect();
      },
      // Log the user out
      logout() {
        this.$auth.logout({
          returnTo: window.location.origin
        });
      },
      cancel() {
        this.job.company = '';
        this.newJob = false;
      }
    },
    async created() {
      const instance = getInstance();

      instance.$watch("loading", async loading => {
        if (!loading && instance.isAuthenticated) {
          await instance.getTokenSilently();
          this.$store.dispatch('getJobs', instance.user.sub.split('|')[1])
          this.loading = false;
        }
      });
    }
  }
</script>