<template>
  <v-container class="py-8 px-6" fluid>
    <v-dialog v-model="showJob">
      <v-card class="mx-auto pa-5" width="50vw">
        <h1>Create Interview</h1>
        <form @submit.prevent="submitInterviewChange">
          <v-text-field label="Interviewers" outlined :value="activeInterviewers" v-model="activeInterviewers">
          </v-text-field>
          <v-menu ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="date"
            transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field v-model="date" label="Date Of Interview" prepend-icon="mdi-calendar" readonly v-bind="attrs"
                v-on="on">
              </v-text-field>
            </template>
            <v-date-picker v-model="date" no-title scrollable>
              <v-spacer></v-spacer>
              <v-btn text color="primary" @click="menu = false">
                Cancel
              </v-btn>
              <v-btn text color="primary" @click="$refs.menu.save(date)">
                OK
              </v-btn>
            </v-date-picker>
          </v-menu>
          <v-textarea outlined name="input-7-4" label="Notes" :value="activeNotes" v-model="activeNotes">
          </v-textarea>
          <v-btn class="mr-4" type="submit">
            submit
          </v-btn>
          <v-btn @click="clear">
            clear
          </v-btn>
        </form>
      </v-card>
    </v-dialog>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-expansion-panels accordion>
            <v-expansion-panel v-for="job in jobs" :key="job._id">
              <v-expansion-panel-header>
                <v-row>
                  <v-col cols='[$vuetify.breakpoint.smAndDown ? "12" : "6"]'>
                    <p>
                      {{job.company}} - {{job.position}}
                    </p>
                  </v-col>
                  <v-col cols='6' class='text-right' v-if='!$vuetify.breakpoint.smAndDown'>
                    <p>
                      Applied on: {{job.appliedOn}}
                    </p>
                  </v-col>
                </v-row>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-expansion-panels accordion>
                  <v-expansion-panel v-for="(interview, i) in job.interviews" :key="i">
                    <v-expansion-panel-header>{{interview.interviewers}}</v-expansion-panel-header>
                    <v-expansion-panel-content>
                      <h3>{{interview.interviewDate}}</h3>
                      <p v-html='interview.notes.replace(/\n/g, "<br>")'></p>
                      <br />
                      <v-btn color='primary' text @click='editInterview(i, job._id)'>Edit Interview</v-btn>
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-expansion-panels>
                <v-btn color="primary" text @click='addInterview(job._id)'>
                  Add Interview
                </v-btn>

                <v-btn color="red" text @click="archive(job)">
                  Archive Job
                </v-btn>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ error }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>

  export default {
    name: 'ActiveJobs',
    data: () => ({
      error: '',
      user: {},
      activeJob: {},
      showJob: false,
      snackbar: false,
      timeout: 2000,
      interviewIndex: 0,
      activeInterviewers: '',
      activeNotes: '',
      newInterview: false,
      date: new Date().toISOString().substr(0, 10),
    }),
    methods: {
      editInterview(index, jobId) {
        this.activeJob = this.jobs.find(job => job._id == jobId);
        this.interviewIndex = index;
        this.activeInterviewers = this.activeJob.interviews[index].interviewers;
        this.activeNotes = this.activeJob.interviews[index].notes;
        this.date = this.activeJob.interviews[index].interviewDate;
        this.showJob = true;
      },
      clear() {
        this.activeInterviewers = ''
        this.activeNotes = ''
      },
      addInterview(jobId) {
        this.activeJob = this.jobs.find(job => job._id == jobId)
        this.activeInterviewers = ''
        this.activeNotes = ''
        this.date = new Date().toISOString().substr(0, 10);
        this.showJob = true;
        this.newInterview = true;
      },
      async submitInterviewChange() {
        if (!this.newInterview) {
          this.activeJob.interviews[this.interviewIndex].notes = this.activeNotes;
          this.activeJob.interviews[this.interviewIndex].interviewers = this.activeInterviewers;
        } else {
          let newInterview = {
            Notes: this.activeNotes,
            interviewers: this.activeInterviewers
          }
          this.activeJob.interviews.push(newInterview);
        }
        try {
          console.log(this.activeJob);
          this.$store.dispatch('updateJob', this.activeJob);
        } catch (err) {
          console.log(err)
        }
      },
      async archive(job) {
        job.archived = true;
        try {
          this.$store.dispatch('updateJob', job);
        } catch (err) {
          console.log(err)
        }
      },
      // Log the user in
      login() {
        this.$auth.loginWithRedirect();
      },
    },
    computed: {
      jobs: function () {
        return this.$store.state.activeJobs
      }
    }
  }
</script>